body {
  background-color: #fff;
  font-family: Roboto, sans-serif;
  font-size: 20px;
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

.container {
  margin: 0 auto;
  padding-left: 32px;
  padding-right: 32px;
}

@media screen and (max-width: 479.98px) {
  .container {
    width: 100%;
  }
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
  }
}

@media screen and (min-width: 1680px) {
  .container {
    width: 1280px;
  }
}

.section {
  padding-top: 60px;
  padding-bottom: 60px;
}

.home__header {
  justify-content: space-around;
  padding-top: 40px;
  padding-bottom: 80px;
  display: flex;
}

.header__logo {
  font-size: 30px;
  font-weight: 500;
  line-height: 1.16;
}

.header__nav-link {
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.16;
}

.header__nav-link:not(:last-child) {
  margin-right: 39px;
}

.library__header {
  justify-content: space-around;
  padding-top: 40px;
  padding-bottom: 60px;
  display: flex;
}

.footer {
  justify-content: center;
  padding: 30px 0;
  display: flex;
}

.footer__button {
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  text-decoration: underline;
}

.footer__text {
  color: #545454;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.18;
}

.card__list {
  flex-wrap: wrap;
  margin-top: 60px;
  margin-bottom: -30px;
  margin-left: -30px;
  display: flex;
}

.card__item {
  flex-basis: 100%;
  margin-bottom: 15px;
  margin-left: 15px;
  transition-property: transform;
  transition-duration: .25s;
  position: relative;
  overflow: hidden;
}

.card__item:hover, .card__item:focus {
  transform: scale(1.03);
}

@media screen and (min-width: 768px) {
  .card__item {
    flex-basis: calc(50% - 15px);
  }
}

@media screen and (min-width: 1200px) {
  .card__item {
    flex-basis: calc(33.3333% - 15px);
  }
}

.card__poster {
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.card__link {
  text-decoration: none;
  display: block;
}

.btn-load-more.is-hidden {
  display: none;
}

.btn-load-more {
  font: inherit;
  color: #fff;
  cursor: pointer;
  background-color: #3f57b3;
  border: none;
  border-radius: 5px;
  margin: 25px auto 0;
  padding: 8px 20px;
  display: block;
  box-shadow: 0 1px 2px rgba(60, 64, 67, .3), 0 2px 6px 2px rgba(60, 64, 67, .15);
}

.btn-to-top {
  z-index: 1000;
  width: 50px;
  height: 50px;
  color: #fff;
  cursor: pointer;
  opacity: 0;
  visibility: hidden;
  background: #3f57b3;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  bottom: 3vh;
  right: 1vw;
  box-shadow: 0 1px 2px rgba(60, 64, 67, .3), 0 2px 6px 2px rgba(60, 64, 67, .15);
}

.btn-to-top--visible {
  opacity: 1;
  visibility: visible;
}

.btn-to-top:hover {
  background-color: #2b3e88;
  transition: all .3s;
}

.card__title {
  text-transform: uppercase;
  margin-top: 10px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
}

